/* Design System Color Variables */
:root {
  /* Primary Colors - Orange */
  --primary-100: #FFF2EC;
  --primary-400: #FF640C;
  --primary-500: #FF5C2C;
  --primary-600: #E65014;
  --primary-700: #D94912;
  
  /* Secondary Colors */
  --yellow-400: #FFED20;
  --yellow-500: #FFE500;
  --yellow-600: #F0CE00;
  --yellow-700: #D9B816;
  
  --blue-400: #5BA7FF;
  --blue-500: #2E8BFF;
  --blue-dark-500: #1E4FD1;
  --blue-dark2-500: #0B2B75;
  
  --purple-500: #A273CC;
  --purple-600: #8B5FCF;
  --purple-700: #8224AB;
  --purple-800: #62388A;
  
  --pink-400: #E794CC;
  --pink-500: #E36BC8;
  --pink-600: #D576AC;
  
  /* Accent Colors */
  --green-400: #7FCE65;
  --green-500: #56A458;
  
  --teal-400: #7FCFC0;
  --teal-500: #75BEB4;
  
  --success-500: #37F652;
  --success-600: #35CE45;
  
  --error-400: #FF5858;
  --error-500: #E13E3E;
  
  /* Neutral Colors */
  --white: #FFFFFF;
  --gray-50: #F9FAFB;
  --gray-100: #F3F4F6;
  --gray-200: #E5E7EB;
  --gray-300: #D1D5DB;
  --gray-400: #9CA3AF;
  --gray-500: #6B7280;
  --gray-600: #4B5563;
  --gray-700: #374151;
  --gray-800: #1F2937;
  --gray-900: #111827;
  
  /* Typography Colors */
  --text-high-contrast: #000000;
  --text-main: #393938;
  --text-secondary: #5B6073;
  --text-hierarchy: #666666;
  --text-hierarchy-2: #777675;
  --text-disabled: #797877;
  --text-miscellaneous: #9EA3B1;
  --text-white: #FFFFFF;
  
  /* Typography Scale */
  --font-family: "Hanken Grotesk", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif;
  --font-size-xs: 0.75rem;    /* 12px */
  --font-size-sm: 0.875rem;   /* 14px */
  --font-size-base: 1rem;     /* 16px */
  --font-size-lg: 1.125rem;   /* 18px */
  --font-size-xl: 1.25rem;    /* 20px */
  --font-size-2xl: 1.5rem;    /* 24px */
  --font-size-3xl: 1.75rem;   /* 28px */
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --line-height-tight: 1.25;
  --line-height-snug: 1.375;
  --line-height-normal: 1.5;
  --line-height-relaxed: 1.625;
}

/* Ensure full viewport coverage */
html, body {
  min-height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(135deg, var(--primary-500) 0%, var(--primary-400) 25%, var(--yellow-500) 50%, var(--teal-400) 75%, var(--teal-500) 100%);
  background-attachment: fixed;
}

.App {
  text-align: center;
  min-height: 100vh;
  background: transparent;
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  color: var(--text-main);
  line-height: var(--line-height-normal);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow-x: hidden;
  padding: 20px 0;
}

.App::before {
  content: 'ðŸ‘¾';
  position: absolute;
  top: 10%;
  left: 10%;
  width: auto;
  height: auto;
  font-size: 6rem;
  opacity: 0.15;
  animation: slowHover 60s ease-in-out infinite;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

.App::after {
  content: 'ðŸŽƒ';
  position: absolute;
  top: 20%;
  right: 15%;
  font-size: 4rem;
  opacity: 0.12;
  animation: floatPumpkin 45s ease-in-out infinite;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

/* Additional floating monsters */
.App:before:nth-of-type(1) { animation-delay: -10s; }
.App:after:nth-of-type(1) { animation-delay: -15s; }

/* Ghost floating in top right */
.floating-ghost {
  content: 'ðŸ‘»';
  position: absolute;
  top: 5%;
  right: 25%;
  font-size: 3.5rem;
  opacity: 0.1;
  animation: floatGhost 35s ease-in-out infinite;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

/* Skull floating in bottom left */
.floating-skull {
  content: 'ðŸ’€';
  position: absolute;
  bottom: 15%;
  left: 8%;
  font-size: 3rem;
  opacity: 0.13;
  animation: floatSkull 50s ease-in-out infinite reverse;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

/* Robot monster floating in middle right */
.floating-robot {
  content: 'ðŸ¤–';
  position: absolute;
  top: 45%;
  right: 5%;
  font-size: 4.5rem;
  opacity: 0.11;
  animation: floatRobot 40s ease-in-out infinite;
  animation-delay: -20s;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

/* Demon floating in bottom right */
.floating-demon {
  content: 'ðŸ‘¹';
  position: absolute;
  bottom: 25%;
  right: 30%;
  font-size: 3.8rem;
  opacity: 0.12;
  animation: floatDemon 42s ease-in-out infinite;
  animation-delay: -25s;
  pointer-events: none;
  z-index: 0;
  will-change: transform;
}

@keyframes slowHover {
  0% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  25% {
    transform: translate3d(60vw, 10vh, 0) rotate(5deg);
  }
  50% {
    transform: translate3d(70vw, 70vh, 0) rotate(-3deg);
  }
  75% {
    transform: translate3d(10vw, 60vh, 0) rotate(8deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
}

/* Individual animation keyframes for each monster */
@keyframes floatPumpkin {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  25% {
    transform: translate3d(-30vw, 15vh, 0) rotate(-8deg);
  }
  50% {
    transform: translate3d(-50vw, -20vh, 0) rotate(5deg);
  }
  75% {
    transform: translate3d(-20vw, -35vh, 0) rotate(-12deg);
  }
}

@keyframes floatGhost {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  30% {
    transform: translate3d(-40vw, 25vh, 0) rotate(6deg);
  }
  60% {
    transform: translate3d(-25vw, 50vh, 0) rotate(-4deg);
  }
  90% {
    transform: translate3d(-10vw, 30vh, 0) rotate(10deg);
  }
}

@keyframes floatSkull {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  20% {
    transform: translate3d(35vw, -10vh, 0) rotate(-5deg);
  }
  50% {
    transform: translate3d(60vw, -25vh, 0) rotate(8deg);
  }
  80% {
    transform: translate3d(40vw, -40vh, 0) rotate(-3deg);
  }
}

@keyframes floatRobot {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  33% {
    transform: translate3d(-25vw, -15vh, 0) rotate(4deg);
  }
  66% {
    transform: translate3d(-45vw, 20vh, 0) rotate(-7deg);
  }
}

@keyframes floatDemon {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  40% {
    transform: translate3d(-30vw, -30vh, 0) rotate(9deg);
  }
  80% {
    transform: translate3d(-15vw, -10vh, 0) rotate(-6deg);
  }
}

/* Respect user's motion preferences and performance */
@media (prefers-reduced-motion: reduce) {
  .App::before,
  .App::after,
  .menu-container,
  .game-container,
  .results-container,
  .mode-button::before {
    animation: none;
  }
}

/* Mobile-first optimizations */
@media (max-width: 768px) {
  .App {
    /* Ensure consistent font rendering on all mobile devices */
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Arial', sans-serif !important;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* Better mobile viewport handling */
    min-height: 100vh;
    min-height: -webkit-fill-available;
  }
  
  /* Disable expensive animations on mobile */
  .App::after {
    display: none;
  }
  .menu-container, .game-container, .results-container {
    animation: none;
    padding: 20px;
    width: 95%;
    margin: 0 10px;
    /* Ensure proper vertical centering on mobile */
    margin-top: auto;
    margin-bottom: auto;
  }
  .mode-button::before {
    animation: none;
  }
  
  /* Optimize text sizes for mobile */
  .menu-container h1, .results-container h1 {
    font-size: 2.8rem;
  }
  
  .question {
    font-size: 2.5rem;
  }
  
  .answer-input {
    width: 180px;
    font-size: 1.2rem;
  }
  
  .mode-button {
    font-size: 1.2rem;
    padding: 20px 25px;
  }
  
  /* Ensure floating elements don't interfere on mobile */
  .floating-ghost, .floating-skull, .floating-robot, .floating-demon {
    font-size: 2.5rem;
    opacity: 0.08;
  }
}

/* Extra small mobile devices */
@media (max-width: 480px) {
  .menu-container, .game-container, .results-container {
    padding: 15px;
    width: 98%;
    margin: 0 5px;
  }
  
  .menu-container h1, .results-container h1 {
    font-size: 1.8rem;
  }
  
  .question {
    font-size: 2rem;
  }
  
  .answer-input {
    width: 150px;
    font-size: 1.1rem;
  }
}

/* Disable expensive animations on lower-end devices */
@media (max-height: 600px) {
  .menu-container, .game-container, .results-container {
    animation: none;
  }
  .mode-button::before {
    animation: none;
  }
}

/* Pause animations when user is actively typing */
body:has(input:focus) .App::before,
body:has(input:focus) .App::after,
body:has(input:focus) .floating-ghost,
body:has(input:focus) .floating-skull,
body:has(input:focus) .floating-robot,
body:has(input:focus) .floating-demon,
body:has(input:focus) .menu-container,
body:has(input:focus) .game-container,
body:has(input:focus) .results-container,
body:has(input:focus) .mode-button::before {
  animation-play-state: paused;
}

/* Performance optimization for input interactions */
.answer-input, .name-input {
  contain: layout style;
}

.answer-input:focus, .name-input:focus {
  contain: layout style paint;
}

/* Aggressive performance mode - disable all animations during any user interaction */
.App:hover .App::before,
.App:hover .App::after,
.App:hover .floating-ghost,
.App:hover .floating-skull,
.App:hover .floating-robot,
.App:hover .floating-demon,
.App:hover .menu-container,
.App:hover .game-container,
.App:hover .results-container {
  animation-play-state: paused;
}

/* Optimize button transitions for immediate response */
.mode-button {
  transition: transform 0.1s ease, filter 0.1s ease;
}

/* Multiplayer Styles */
.mode-button.multiplayer {
  background: var(--white);
  border-color: var(--purple-500);
  color: var(--purple-500);
}

.mode-button.multiplayer:hover {
  background: var(--purple-500);
  color: var(--white);
  border-color: var(--purple-500);
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 0 2px 4px rgba(0, 0, 0, 0.06);
}

.mode-button.multiplayer:hover .mode-description {
  color: var(--white);
}

.mode-button.teacher {
  background: var(--white);
  border-color: var(--green-500);
  color: var(--green-500);
}

.mode-button.teacher:hover {
  background: var(--green-400);
  color: var(--white);
  border-color: var(--green-500);
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 0 2px 4px rgba(0, 0, 0, 0.06);
}

.mode-button.teacher:hover .mode-description {
  color: var(--white);
}

.mode-button.student {
  background: var(--white);
  border-color: var(--blue-500);
  color: var(--blue-500);
}

.mode-button.student:hover {
  background: var(--blue-500);
  color: var(--white);
  border-color: var(--blue-500);
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 0 2px 4px rgba(0, 0, 0, 0.06);
}

.mode-button.student:hover .mode-description {
  color: var(--white);
}

.mode-button.back {
  background: var(--white);
  border-color: var(--gray-300);
  color: var(--text-high-contrast);
}

.mode-button.back:hover {
  background: var(--gray-100);
  border-color: var(--gray-400);
  color: var(--text-main);
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 0 2px 4px rgba(0, 0, 0, 0.06);
}

.mode-button.danger {
  background: var(--white);
  border-color: var(--error-500);
  color: var(--error-500);
  font-weight: var(--font-weight-semibold);
}

.mode-button.danger:hover {
  background: var(--error-500);
  color: var(--white);
  border-color: var(--error-500);
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 0 2px 4px rgba(0, 0, 0, 0.06);
}

.mode-button.danger:hover .mode-description {
  color: var(--white);
}

.session-setup {
  margin: 20px 0;
}

.session-setup h3 {
  color: #374151;
  margin-bottom: 15px;
}

.mode-selection {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

.mode-option {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.2s ease;
}

.mode-option:hover {
  background: rgba(255, 255, 255, 0.9);
}

.mode-option input[type="radio"] {
  margin: 0;
}

.session-join {
  margin: 20px 0;
}

.session-join label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
  color: #374151;
}

.session-code-input {
  width: 150px;
  padding: 15px;
  font-size: 1.5rem;
  text-align: center;
  border: 3px solid #ff6b35;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.9);
  font-weight: bold;
  letter-spacing: 0.2em;
}

.session-code-input:focus {
  outline: none;
  border-color: #f7931e;
  box-shadow: 0 0 10px rgba(247, 147, 30, 0.3);
}

.session-info {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  padding: 20px;
  margin-bottom: 20px;
}

.session-info h2 {
  color: #374151;
  margin-bottom: 10px;
}

.session-code-display {
  font-family: 'Courier New', monospace;
  background: #f3f4f6;
  padding: 5px 10px;
  border-radius: 5px;
  font-weight: bold;
  font-size: 1.2em;
  color: #dc2626;
}

.students-list {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  padding: 20px;
  margin-bottom: 20px;
}

.students-list h3 {
  color: #374151;
  margin-bottom: 15px;
}

.students-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 10px;
}

.student-card {
  background: #f8fafc;
  border: 3px solid #e2e8f0;
  border-radius: 10px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.student-name {
  font-weight: bold;
  color: #374151;
}

.student-status {
  color: #059669;
  font-size: 0.9em;
}

.no-students {
  color: #6b7280;
  font-style: italic;
  text-align: center;
  padding: 20px;
}

.waiting-message {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  padding: 20px;
  margin-bottom: 20px;
}

.waiting-message p {
  color: #374151;
  font-size: 1.2em;
  margin: 0;
}

/* Live Leaderboard */
.multiplayer-leaderboard {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 15px;
  padding: 15px;
  margin-bottom: 20px;
  border: 3px solid #ffd23f;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.multiplayer-leaderboard h3 {
  color: #374151;
  margin: 0 0 15px 0;
  font-size: 1.2em;
}

.leaderboard-grid {
  display: flex;
  flex-direction: column;
  gap: 8px;
  max-height: 200px;
  overflow-y: auto;
}

.leaderboard-entry {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px 12px;
  background: #f8fafc;
  border-radius: 8px;
  border: 2px solid transparent;
  transition: all 0.2s ease;
}

.leaderboard-entry.current-player {
  background: linear-gradient(135deg, #fef3c7, #fbbf24);
  border-color: #f59e0b;
  transform: scale(1.02);
}

.leaderboard-entry.top-three {
  background: linear-gradient(135deg, #ecfdf5, #10b981);
  border-color: #059669;
}

.leaderboard-entry.top-three.current-player {
  background: linear-gradient(135deg, #fef3c7, #fbbf24);
  border-color: #f59e0b;
}

.rank {
  font-size: 1.2em;
  font-weight: bold;
  min-width: 35px;
  text-align: center;
}

.player-info {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.player-name {
  font-weight: 600;
  color: #374151;
}

.player-stats {
  display: flex;
  gap: 10px;
  align-items: center;
}

.score-display {
  background: #e5e7eb;
  padding: 3px 8px;
  border-radius: 12px;
  font-size: 0.9em;
  font-weight: bold;
  color: #374151;
}

.streak-display {
  background: #fef3c7;
  color: #f59e0b;
  padding: 3px 6px;
  border-radius: 10px;
  font-size: 0.8em;
  font-weight: bold;
}

.session-indicator {
  border-color: #8b5cf6;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.15), rgba(124, 58, 237, 0.15));
  /* Margin removed - consistent with other header elements */
}

.streak {
  color: #f59e0b;
  font-weight: bold;
  margin-left: 15px;
}

/* Compact game stats for multiplayer */
.compact-game-stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}

.compact-game-stats .timer,
.compact-game-stats .score,
.compact-game-stats .session-indicator {
  background: rgba(255, 255, 255, 0.95);
  color: #374151;
  padding: 8px 12px;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  border: 2px solid #e2e8f0;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  min-height: auto;
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
  flex-shrink: 0;
}

/* Mobile optimizations for multiplayer */
@media (max-width: 768px) {
  .compact-game-stats {
    justify-content: center;
    gap: 8px;
  }
  
  .compact-game-stats .timer,
  .compact-game-stats .score,
  .compact-game-stats .session-indicator {
    padding: 6px 10px;
    font-size: 0.85rem;
    border-radius: 6px;
  }
  
  .game-header {
    margin-bottom: 15px;
  }
  
  .multiplayer-leaderboard {
    padding: 12px;
  }
  
  .students-grid {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 8px;
  }
  
  .student-card {
    padding: 10px;
  }
  
  .leaderboard-entry {
    padding: 6px 10px;
  }
  
  .session-code-input {
    width: 120px;
    font-size: 1.3rem;
    padding: 12px;
  }
  
  .mode-selection {
    gap: 8px;
  }
  
  .mode-option {
    padding: 8px;
    font-size: 0.9em;
  }
}

@media (max-width: 480px) {
  .leaderboard-grid {
    max-height: 150px;
  }
  
  .player-stats {
    gap: 5px;
  }
  
  .score-display, .streak-display {
    font-size: 0.8em;
    padding: 2px 6px;
  }
  
  .session-indicator {
    font-size: 0.9em;
    padding: 10px 14px;
    min-height: 40px;
  }
  
  .timer, .score {
    font-size: 0.9em;
    padding: 10px 14px;
    min-height: 40px;
  }
}

/* Teacher Monitor and Results Styles */
.timer-display h2 {
  color: #dc2626;
  font-size: 1.8rem;
  margin: 15px 0;
  padding: 10px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  border: 2px solid #fbbf24;
}

.game-finished h2 {
  color: #059669;
  font-size: 1.6rem;
  margin: 15px 0;
  padding: 10px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  border: 2px solid #10b981;
}

.live-leaderboard-monitor {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 15px;
  padding: 20px;
  margin: 20px 0;
  border: 3px solid #06ffa5;
