    inset 0 1px 0 rgba(255, 255, 255, 0.6);
  max-width: 500px;
  width: 90%;
  margin: 0 20px;
  position: relative;
  z-index: 1;
  animation: none;
  transform: translateZ(0);
  will-change: box-shadow;
}

@keyframes containerGlow {
  0% { 
