      0 25px 50px rgba(255, 71, 87, 0.15),
      0 0 0 1px rgba(255, 255, 255, 0.3),
      inset 0 1px 0 rgba(255, 255, 255, 0.6);
  }
  100% { 
    box-shadow: 
      0 28px 55px rgba(6, 255, 165, 0.2),
      0 0 0 1px rgba(255, 255, 255, 0.3),
      inset 0 1px 0 rgba(255, 255, 255, 0.6);
