  animation: countdownPulse 1s ease-in-out;
}

@keyframes countdownPulse {
  0% { 
    transform: scale(0.8); 
    opacity: 0.7; 
  }
  50% { 
    transform: scale(1.2); 
    opacity: 1; 
  }
  100% { 
    transform: scale(1); 
    opacity: 1; 
  }
}

